
html,body{
  width: 100%;
  height: 100%;
  background: #f0f0f0;
  overflow: hidden;
}
.app-main{
  width: 100%;
  height: 100%;
}

#chat_box {
  width: 100rem;
  margin: 0 auto;
  padding: 1rem;
  border: 1px solid #c3c3c3;
}
#chat_box .chat_top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#chat_box .chat_top .top_left {
  width: 10rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#chat_box .chat_top .top_left .cover {
  width: 5rem;
}
#chat_box .chat_top .top_left .name {
  font-size: 2rem;
  font-weight: bold;
  color: #333333;
}
#chat_box .chat_top .top_right {
  width: 60rem;
  margin-left: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 1rem;
  color: #615656;
}


























































































.coverClass[data-v-5287b85d]{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.login {
  width: 100%;
  height: 100%;
  background-image: url('https://file.iviewui.com/iview-admin/login_bg.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
}
.login-con {
  position: absolute;
  right: 160px;
  top: 50%;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
  width: 300px;
}
.login-con-header {
  font-size: 16px;
  font-weight: 300;
  text-align: center;
  padding: 30px 0;
}
.login-con .form-con {
  padding: 10px 0 0;
}
.login-con .login-tip {
  font-size: 10px;
  text-align: center;
  color: #c3c3c3;
}
.long_left {
  width: 23rem;
  height: 100%;
  background-image: url(/static/img/login_bg.9c58109.jpg);
  background-position: center;
}
.login_content {
  width: 15rem;
  height: 15rem;
  margin-left: 14rem;
}
.login_content .login_title {
  padding: 0;
  color: #FFFFFF;
  font-size: 4rem;
  position: relative;
  height: 4rem;
  margin: 0;
  line-height: 4rem;
}
.login_content .login_title span {
  display: block;
  position: absolute;
  font-size: 1.5rem;
  color: #333333;
  top: 0;
  font-weight: 500;
}
.login_content .login_tips {
  margin-top: 2.5rem;
  font-size: .7rem;
  color: #777777;
  font-weight: 500;
}
.login_content .login_input {
  margin-top: 1rem;
}
.login_content .login_input input {
  height: 2rem;
  width: 12rem;
  margin-top: 1rem;
}
.login_content .login_btn {
  display: block;
  margin-top: 2rem;
  width: 100%;
  height: 2.5rem;
  background: #FF3B3B;
  font-size: .8rem;
  color: #FFFFFF;
  font-weight: 500;
}






























































































